.winning-streak .streak {
    font-size: 3rem;
    text-align: center;
    padding: 13px 16px;
    line-height: 1.5;
    margin: 0;
}

.winning-streak .streak .streak-score:before {
    display: inline-block;
    content: " - ";
    padding: 0 10px;
}

@media (max-width: 650px) {
    .winning-streak .streak span {
        display: block;
    }

    .winning-streak .streak .streak-score:before {
        display: none;
    }
}

.winning-streak .card {
    height: calc(100% - 20px);
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: justify;
        justify-content: space-between;
}

@media (max-width: 768px) {
    .winning-streak .card {
        height: calc(100% - 10px);
    }
}



